import React from 'react';
import PropTypes from 'prop-types'
import {getFormInfo} from "../../scripts/api/contentApi"
import { requestService } from '../../scripts/api/realApi';
import {Session} from '../../scripts/user/Session'
import ToolbarSecondary from '../Toolbars/ToolbarSecondary';
import BaseForm from './BaseForm';
import LoaderScreen from '../LoaderScreen';

export default class FormTask extends React.Component {
    static propTypes = {
        /** 
         * uuid формы
         */
        uuid: PropTypes.number,

        /** 
         * действие при ошибке
         */
        onFail: PropTypes.func,

        /** 
         * Действие при успешной отправке формы
         */
        onSuccess: PropTypes.func,
    }

    constructor(props) {
        super(props);
        this.state = {loading: true}
    }

    onFormSubmit(data) {
        this.setState({loading: true})
        requestService(this.state.session, this.props.uuid, data)
        .then((result) => {this.setState({loading: false}); this.props.onSuccess(result)})
        .catch((error) => {this.setState({loading: false}); this.props.onFail(error)})
    }

    componentDidMount() {
        getFormInfo(this.props.uuid).then(result => {
            this.setState({form: result, session: Session.getCurrent(), loading: false})
        })
        .catch(error => {
            this.props.onFail(error)
        })
    }

    render() {
        if (this.state.loading) {
            return <LoaderScreen/>
        }

        return (
        <div class="max-w-3xl mx-auto">
        <BaseForm
        title={this.state.form.title}
        fields={this.state.form.fields}
        onSubmit={this.onFormSubmit.bind(this)}/>
        
        <div class="-mx-5 my-8">
        <ToolbarSecondary size="lg" buttons={[
            {
                text: "Отмена",
                onClick: (e) => {
                    e.preventDefault();
                    this.props.onFail.call(this);
                },
                //timeout: 60,
            },
            /**
            {
                text: "Другой пользователь",
                onClick: (e) => {
                    e.preventDefault();
                    this.onLogout.call(this)
                    .then((() => this.props.history.push(
                        `/forms/login?success=/forms/service/${serviceId}&fail=/forms/service/${serviceId}`
                    )).call(this))
                },
                color: 'blue-darker'
            },
             */
        ]}/>
        </div>
        </div>
        )
    }
}