import React from 'react';
import PropTypes from 'prop-types'

import MainButton from '../Buttons/MainButton';
import TimeoutButtonOutline from '../Buttons/TimeoutButtonOutline';

import format from '../../scripts/helperFunctions/format';


export default class BaseForm extends React.Component {
    static propTypes = {
        title: PropTypes.string(),
        fields: PropTypes.array(),

        /** 
         * Действие при нажатии кнопки "подтвердить"
         */
        onSubmit: PropTypes.func,
    }

    constructor(props) {
        super(props);
        this.state = {}
    }

    static defaultProps = {
        // Все свойства указывать обязательно!
        // стандартными не заменяем
    }

    render() {
        return (
            <div class="w-full max-w-3xl mx-auto">
            <form class="bg-white shadow-md relative rounded px-8 pt-6 pb-8 mb-4 mt-16">
            <div class="absolute -top-5 -left-5 -right-5 -bottom-5 -z-10 bg-postcard rounded shadow-md"></div>
                
                <div class="mb-8">
                <h4 class="block text-blue-darker font-black text-5xl mb-2 text-center" for="service">
                    {this.props.title}
                </h4>
                </div>
                {
                    this.props.fields.map(field => (
                        <div class="mb-8">
                            <label class="block text-blue-darker text-3xl font-black mb-2" for={field.id}>
                                {field.label}
                            </label>
                            <input
                            class="shadow appearance-none border rounded w-full bg-gray-200 py-4 px-6
                            text-gray-900 text-2xl leading-tight focus:outline-none focus:shadow-outline"
                            id={field.id} name={field.name} type={field.type} value={field.value}
                            onChange={(e) => {
                                let d = {}
                                d[field.name] = e.target.value
                                this.setState(d)
                            }}/>
                        </div>
                        )
                    )
                }

                <div class="grid h-28 grid-cols-1 grid-rows-1">
                <MainButton className="hover:bg-blue-lighter" direction="right" size="lg" onClick={(e) => {
                    e.preventDefault()
                    this.props.onSubmit({...this.state})
                    }}>
                    Заказать
                </MainButton>
                {/*
                <a class="inline-block align-baseline font-bold text-2xl text-blue-lighter
                hover:text-blue-darker" href="#">
                    Ошибка в данных?
                </a>
                */}
                </div>
            </form>
            <p class="text-center text-gray-500 text-lg mt-8">
                &copy; 2021 РМ Рейл
            </p>
            </div>
        );
    }
}